<template>
  <div>
    <div class="main-title">Data Visualization</div>
    <div class="create-transaction-container">
      <div class="btn-container">
        <div class="filter-btn"></div>
        Set Query
      </div>
    </div>
    <!-- Query filter options -->

    <div class="chart-list">
      <!-- Donut income/expenses -->
      <div class="chart-container">
        <div class="chart-title">Incomes/Expenses Total comparation</div>
        <div class="chart pie-chart pie-chart-small">
          <canvas id="total-incomes-expenses-chart"></canvas>
        </div>
      </div>
      
      <!-- Global tendency per account -->
      <div class="chart-container">
        <div class="chart-title">Balance Tendency</div>
        <div class="chart">
          <canvas id="tendency-chart"></canvas>
        </div>
      </div>

      <!-- Historic incomes/expenses -->
      <div class="chart-container">
        <div class="chart-title">Incomes/Expenses Historic</div>
        <div class="chart">
          <canvas id="incomes-expenses-historic-chart"></canvas>
        </div>
      </div>

      <!-- Historic expenses by category -->
      <div class="chart-container">
        <div class="chart-title">Expenses Historic by Category</div>
        <div class="chart" style="min-height: 300px !important;">
          <canvas id="category-expenses-historic-chart"></canvas>
        </div>
      </div>

      <!-- Donut expenses all period -->
      <div class="chart-container">
        <div class="chart-title">Expenses Category Total comparation</div>
        <div class="chart pie-chart">
          <canvas id="total-category-expenses-chart"></canvas>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import TransactionList from '@/components/TransactionList.vue';
import Chart from 'chart.js/auto';

export default {
  name: "QueryView",
  data() {
    return {
      months: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
    }
  },
  components: {
    TransactionList
  },
  computed: {
    ...mapGetters(["uid", "appData", "categoriesList"]),
    filteredDataset() {
      return this.appData
    },
    transactions() {
      const transactions = [];
      Object.values(this.filteredDataset).forEach(x => {
        Object.values(x.transactions).forEach(t => transactions.push(t));
      });
      const sortedTransactions = transactions.sort((a, b) => {
        return new Date(a.date) > new Date(b.date);
      })
      return sortedTransactions;
    },
    datesLabels() {
      const labels = [];

      const initDate = new Date(this.transactions[0].date);
      const endDate = new Date(this.transactions[this.transactions.length-1].date);
      let month = initDate.getMonth();
      let year = initDate.getFullYear();
      while(!((month > endDate.getMonth() && year == endDate.getFullYear()) || year > endDate.getFullYear())) {
        labels.push((month + 1 < 10 ? "0" : "") + (month + 1) + "-" + year.toString().substring(2,4));
        
        year = month == 11 ? year + 1 : year;
        month = month == 11 ? 0 : month + 1;
      }

      return labels;
    },
    dataForIncomesExpensesHistoricChart() {
      const result = this.datesLabels.reduce((r,x) => {
        const dict = r;
        dict[x] = {incomes: 0, expenses: 0};
        return dict;
      }, {});

      Object.values(this.filteredDataset).forEach(x => {
        if(x.transactions) {
          Object.values(x.transactions).forEach(t => {
            const amount = Number(t.amount);
            const tDate = new Date(t.date);
            const month = tDate.getMonth();
            const year = tDate.getFullYear();
            const key = (month + 1 < 10 ? "0" : "") + (month + 1) + "-" + year.toString().substring(2,4);
            
            if(amount > 0) result[key].incomes = result[key].incomes + amount;
            if(amount < 0) result[key].expenses = result[key].expenses - amount;
          })
        }
      });
      return result;
    },
    dataForCategoryExpensesHistoricChart() {
      const result = {};
      Object.keys(this.categoriesList).forEach(k => result[k] = {name: this.categoriesList[k].name})
      Object.values(this.filteredDataset).forEach(x => {
        if(x.transactions) {
          Object.values(x.transactions).forEach(t => {
            const amount = Number(t.amount);
            const tDate = new Date(t.date);
            const key = this.months[tDate.getMonth()] + "(" + tDate.getFullYear() + ")";
            
            if(Object.keys(result).includes(key)) {
              if(amount > 0) result[key].incomes = result[key].incomes + amount;
              if(amount < 0) result[key].expenses = result[key].expenses - amount;
            } else {
              result[key] = {
                incomes: amount > 0 ? amount : 0,
                expenses: amount < 0 ? -amount : 0
              }
            }
          })
        }
      });
      return result;
    },
    dataForTendencyChart() {
      const result = {};
      
      this.transactions.forEach(t => {
        const tDate = new Date(t.date);
        const month = tDate.getMonth();
        const year = tDate.getFullYear();
        const key = (month + 1 < 10 ? "0" : "") + (month + 1) + "-" + year.toString().substring(2,4);
        // Global tendency
        if(result.all && result.all[key]) {
          result.all[key] = result.all[key] + t.amount;
        } else {
          if(!result.all) {
            result.all = {};
          }
          const currentArr = Object.values(result.all);
          const currentAmount = currentArr.length > 0 ? currentArr[currentArr.length - 1] : 0;
          result.all[key] = currentAmount + t.amount;
        }

        // Tendency per account
        if(result[t.bankAccountId] && result[t.bankAccountId][key]) {
          result[t.bankAccountId][key] = result[t.bankAccountId][key] + t.amount;
        } else {
          if(!result[t.bankAccountId]) {
            result[t.bankAccountId] = {};
          }
          const currentArr = Object.values(result[t.bankAccountId]);
          const currentAmount = currentArr.length > 0 ? currentArr[currentArr.length - 1] : 0;
          result[t.bankAccountId][key] = currentAmount + t.amount;
        }
      });

      Object.keys(result).forEach(k => {
        const kDates = Object.keys(result[k]);
        if(kDates.length < this.datesLabels.length) {
          let lastAmount = 0;
          this.datesLabels.forEach(d => {
            const currentValue = result[k][d];
            if(!currentValue) {
              console.log(currentValue, k, d);
              result[k][d] = lastAmount;
            } else {
              lastAmount = currentValue;
            }
          })
        }
      })

      return result;
    },
    dataForTotalIncomesExpensesChart() {
      const result = {incomes: 0, expenses: 0};
      
      this.transactions.forEach(t => {
        if(Number(t.amount) < 0) {
          result.expenses += Number(t.amount) * -1;
        } else if(Number(t.amount) > 0) {
          result.incomes += Number(t.amount);
        }
      });

      return result;
    },
    dataForTotalCategoryExpensesChart() {
      const result = {};
      
      this.transactions.filter(t => Number(t.amount) < 0).forEach(t => {
        const amount = Math.round(Number(t.amount) * -1 * 100) / 100;
        const category = this.categoriesList[t.categoryId].name;
        if(result[category]) {
          result[category] += amount;
        } else {
          result[category] = amount;
        }
      });

      return result;
    },
    dataForCategoryExpensesHistoricChart() {
      const result = Object.values(this.categoriesList).filter(x => x.transactionType == "expense").map(x => x.name).reduce((r,x) => {
        const dict = r;
        dict[x] = this.datesLabels.reduce((r2,x2) => {
          const dict2 = r2;
          dict2[x2] = 0;
          return dict2
        }, {});
        return dict
      }, {});

      this.transactions.filter(t => Number(t.amount) < 0).forEach(t => {
        const amount = Math.round(Number(t.amount) * -1 * 100) / 100;;
        const category = this.categoriesList[t.categoryId].name;
        const tDate = new Date(t.date);
        const key = (tDate.getMonth() + 1 < 10 ? "0" : "") + (tDate.getMonth() + 1) + "-" + tDate.getFullYear().toString().substring(2,4);
        if(result[category][key]) {
          result[category][key] = result[category][key] + amount;
        } else {
          result[category][key] = amount;
        }
      });

      return result;
    },
  },
  methods: {
    setCharts() {
      this.setIncomesExpensesHistoricChart();
      this.setCategoryExpensesHistoricChart();
      this.setTendencyChart();
      this.setTotalIncomesExpensesChart();
      this.setTotalCategoryExpensesChart();
    },
    setIncomesExpensesHistoricChart() {
      const ctx = document.querySelector("#incomes-expenses-historic-chart");

      new Chart(
        ctx,
        {
          type: 'line',
          data: {
            labels: this.datesLabels,
            datasets: [
              {
                label: 'Incomes',
                data: Object.values(this.dataForIncomesExpensesHistoricChart).map(x => x.incomes)
              },
              {
                label: 'Expenses',
                data: Object.values(this.dataForIncomesExpensesHistoricChart).map(x => x.expenses)
              },
              {
                label: 'Results',
                data: Object.values(this.dataForIncomesExpensesHistoricChart).map(x => x.incomes - x.expenses)
              }
            ],
          },
          options: {
            scales: {
              x: {
                ticks: {
                  font: {size: 10}
                }
              },
              y: {
                grid: {
                  color: (ctx) => (ctx.tick.value === 0 ? 'black' : Chart.defaults.borderColor),
                  lineWidth: (ctx) => (ctx.tick.value === 0 ? 2 : 1),
                }
              }
            }
          }
        },
      );
    },
    setTendencyChart() {
      const ctx = document.querySelector("#tendency-chart");

      new Chart(
        ctx,
        {
          type: 'line',
          data: {
            labels: this.datesLabels,
            datasets: [
              {
                label: 'All',
                data: Object.values(this.dataForTendencyChart.all)
              },
              ...Object.keys(this.appData).map(k => {
                return {
                  label: this.appData[k].name,
                  data: Object.values(this.dataForTendencyChart[k])
                }
              })
            ],
          },
          options: {
            scales: {
              x: {
                ticks: {
                  font: {size: 10}
                }
              }
            },
            maintainAspectRatio: true
          }
        },
      );
    },
    setTotalIncomesExpensesChart() {
      const ctx = document.querySelector("#total-incomes-expenses-chart");

      new Chart(
        ctx,
        {
          type: 'pie',
          data: {
            labels: Object.keys(this.dataForTotalIncomesExpensesChart),
            datasets: [{
              data: Object.values(this.dataForTotalIncomesExpensesChart)
            }]
          },
          options: {
            maintainAspectRatio: true
          }
        },
      );
    },
    setTotalCategoryExpensesChart() {
      const ctx = document.querySelector("#total-category-expenses-chart");
      new Chart(
        ctx,
        {
          type: 'pie',
          data: {
            labels: Object.keys(this.dataForTotalCategoryExpensesChart),
            datasets: [{
              data: Object.values(this.dataForTotalCategoryExpensesChart)
            }]
          },
          options: {
            maintainAspectRatio: true
          }
        },
      );
    },
    setCategoryExpensesHistoricChart() {
      const ctx = document.querySelector("#category-expenses-historic-chart");
      const data = this.dataForCategoryExpensesHistoricChart.result;
      
      new Chart(
        ctx,
        {
          type: 'line',
          data: {
            labels: this.datesLabels,
            datasets: Object.keys(this.dataForCategoryExpensesHistoricChart).map(k => {
              return {
                label: k,
                data: Object.values(this.dataForCategoryExpensesHistoricChart[k]),
              }
            })
          },
          options: {
            maintainAspectRatio: false,
            scales: {
              x: {
                ticks: {
                  font: {size: 10}
                }
              }
            }
          }
        },
      );
    },
  },
  mounted() {
    if(this.uid.length == 0 || Object.values(this.appData).length == 0) this.$router.replace({name: "home"});
    if(Object.values(this.appData).length != 0) this.setCharts();
  },
}
</script>

